'use client';

import { useEffect, useState } from 'react';
import socket from '@/lib/socket';
import { toast } from 'sonner';

export interface NotificationItem {
  id: number;
  type: string;
  data: any;
  read?: boolean;
}

export default function useNotifications() {
  const [items, setItems] = useState<NotificationItem[]>([]);

  useEffect(() => {
    try {
      socket.connect();
    } catch (e) {
      // ignore
    }

    const handleBookingPending = (payload: any) => {
      const item: NotificationItem = {
        id: Date.now(),
        type: 'booking:pending',
        data: payload,
        read: false,
      };
      setItems(prev => [item, ...prev]);
      toast.success('Booking baru perlu konfirmasi');
    };

    socket.on('booking:pending', handleBookingPending);

    return () => {
      socket.off('booking:pending', handleBookingPending);
      try { socket.disconnect(); } catch(e) {}
    };
  }, []);

  const markAllRead = () => setItems(prev => prev.map(i => ({ ...i, read: true })));

  return { items, setItems, markAllRead };
}
