'use client';

import { io } from 'socket.io-client';

const WS_URL = process.env.NEXT_PUBLIC_WS_URL || '';

const socket = io(WS_URL, {
  autoConnect: false,
  auth: {
    token: typeof window !== 'undefined' ? localStorage.getItem('access_token') : null,
  },
});

export default socket;
